<script>
import bus from '../bus'
import {
  Icon,
  Select,
  Option,
  OptionGroup,
  Radio,
  Field,
  Form,
  Button
} from 'veui'
import type from 'veui/managers/type'
import 'vue-awesome/icons/flag'
import 'vue-awesome/icons/gift'
import 'veui-theme-dls-icons/info-circle'

export default {
  name: 'select-demo',
  components: {
    'veui-select': Select,
    'veui-option': Option,
    'veui-option-group': OptionGroup,
    'veui-radio': Radio,
    'veui-icon': Icon,
    'veui-field': Field,
    'veui-form': Form,
    'veui-button': Button
  },
  data () {
    return {
      filter: ({ label }) => {
        return label.indexOf(this.keyword) !== -1
      },
      keyword: '',
      phone: null,
      hero: ['baidu', 'tencent'],
      disabled: true,
      complex: ['1', '2'],
      complex2: [],
      loaded: false,
      icon: true,
      defaultValue: null,
      defaultMultiValue: null,
      defaultSearchMultiValue: [1, 3],
      defaultValue1: null,
      defaultValue2: 2,
      defaultValue3: null,
      defaultValue4: null,
      defaultValue5: null,
      defaultValue6: null,
      defaultValue7: null,
      defaultValue8: '1-1',
      defaultValue9: null,
      defaultValue10: null,
      sizes: ['xs', 's', 'm', 'l'],
      attrs: {
        name: 'age',
        readonly: false,
        disabled: false,
        clearable: true,
        options: [
          {
            label: '111',
            value: 1
          },
          {
            label: '112',
            value: 2
          },
          {
            label: '122',
            value: 3,
            disabled: true
          },
          {
            label: '222',
            value: 4
          }
        ]
      },
      optGroupAttrs: {
        name: 'name',
        readonly: false,
        disabled: false,
        clearable: true,
        options: [
          {
            label: '1',
            position: 'popup',
            options: [
              {
                label: '1',
                value: '1-1'
              },
              {
                label: '2',
                value: '1-2'
              },
              {
                label: '3',
                value: '1-3'
              }
            ]
          },
          {
            label: '2',
            options: [
              {
                label: '1',
                value: '2-1'
              },
              {
                label: '2',
                value: '2-2'
              },
              {
                label: '3',
                value: '2-3'
              }
            ]
          },
          {
            label: '3',
            options: [
              {
                label: '1',
                value: '3-1'
              },
              {
                label: '2',
                value: '3-2'
              },
              {
                label: '3',
                value: '3-3'
              }
            ]
          },
          {
            label: '4',
            options: [
              {
                label: '1',
                value: '4-1'
              },
              {
                label: '2',
                value: '4-2'
              },
              {
                label: '3',
                value: '4-3'
              }
            ]
          }
        ]
      },
      searchResult: null,
      complexData: [
        {
          label: '',
          options: [
            { label: '', value: '1' },
            { label: '', value: '2' },
            { label: '', value: '3' }
          ]
        },
        {
          label: '',
          options: [
            {
              label: '',
              options: [{ label: 'A', value: 'a' }]
            },
            {
              label: '',
              options: [
                { label: '', value: '4' },
                { label: '', value: '5' },
                { label: '', value: '6' },
                { label: '', value: '7' },
                { label: '', value: '8' },
                { label: '', value: '9' },
                { label: '', value: '10' },
                { label: '', value: '11' }
              ]
            },
            {
              label: '',
              options: [{ label: 'B', value: 'b' }]
            },
            {
              label: '',
              options: [{ label: 'C', value: 'c' }]
            },
            {
              label: '',
              options: [{ label: 'D', value: 'd' }]
            },
            {
              label: '',
              options: [{ label: 'E', value: 'e' }]
            },
            {
              label: '',
              options: [{ label: 'F', value: 'f' }]
            },
            {
              label: '',
              options: [{ label: 'G', value: 'g' }]
            }
          ]
        },
        {
          label: '',
          options: [
            { label: '', value: '12' },
            { label: '', value: '13' },
            { label: '', value: '14' },
            { label: '', value: '15' },
            { label: '', value: '16' },
            { label: '', value: '17' }
          ]
        }
      ]
    }
  },
  computed: {
    result () {
      return [
        { label: 'Foo', value: 'foo' },
        { label: 'Bar', value: 'bar' },
        { label: this.keyword, value: this.keyword }
      ]
    },
    groupedOpts () {
      return type
        .clone(this.optGroupAttrs.options)
        .map(group => {
          delete group.label
          delete group.position
          return group
        })
        .reduce((acc, cur) => {
          acc.push(cur)
          return acc
        }, [])
    },
    complexResult () {
      return this.searchResult
        ? (this.complex || [])
          .map(v => ({
            label: v + '**',
            value: v
          }))
          .concat(this.searchResult || [])
        : null
    }
  },
  mounted () {
    this.$children.forEach(child => {
      child.$on('click', () => {
        bus.$emit('log', child.$el.getAttribute('ui'))
      })
    })

    this.loaded = true
  },
  methods: {
    change (value) {
      // alert('' + value)
    },
    handleSearch (val) {
      if (val) {
        this.searchResult = Array.from({
          length: 5
        }).map((_, i) => ({
          label: `${val}#${i}`,
          value: `${val}#${i}`
        }))
      } else {
        this.searchResult = null
      }
    }
  }
}
</script>

