<template>
<article>
  <h1>Plugins</h1>
  <h2>
    <code>this.$confirm()</code>
  </h2>
  <section>
    <button
      v-if="!removed"
      @click="handleButtonClick"
    ></button>
    <button @click="handleButton2Click"></button>
  </section>
</article>
</template>

<script>
export default {
  name: 'plugins-demo',
  components: {},
  data () {
    return {
      removed: false
    }
  },
  methods: {
    async handleButtonClick () {
      let ok = await this.$confirm('', '')
      if (!ok) {
        return
      }
      this.removed = true
    },
    async handleButton2Click () {
      await this.$confirm('', '', {
        ok: () => {
          let wait = new Promise(resolve => setTimeout(resolve, 1000))
          return wait.then(() => {
            if (Math.random() > 0.7) {
              this.$toast.error('')
              return false
            }
            this.removed = false
          })
        }
      })
    }
  }
}
</script>
