<template>
<article>
  <h1>
    <code>&lt;veui-autocomplete&gt;</code>
  </h1>
  <section>
    <h2>(m)</h2>
    <veui-autocomplete
      v-model="inputValue"
      :datasource="suggestions"
    />
  </section>
  <section>
    <h2>(s)</h2>
    <veui-autocomplete
      v-model="inputValue"
      ui="s"
      clearable
      :datasource="suggestions"
    />
  </section>
  <section>
    <h2>(l)</h2>
    <veui-autocomplete
      v-model="inputValue"
      ui="l"
      :datasource="suggestions"
      :suggest-trigger="['focus', 'input']"
      disabled
    />
  </section>
  <section>
    <h2>(xs)</h2>
    <veui-autocomplete
      v-model="inputValue"
      ui="xs"
      :datasource="suggestions"
      :suggest-trigger="['focus', 'input']"
      readonly
    />
  </section>
  <section>
    <h2></h2>
    <veui-autocomplete
      v-model="inputValue"
      invalid
      :datasource="suggestions"
      :suggest-trigger="['focus', 'input']"
    />
  </section>
  <section>
    <h2>input </h2>
    <veui-autocomplete
      v-model="value"
      :datasource="suggestions"
      placeholder=""
    />
  </section>
  <section>
    <h2>focus 4</h2>
    <veui-autocomplete
      v-model="treeValue"
      :datasource="treeSuggestions"
      placeholder=""
      suggest-trigger="focus"
      :overlay-style="{
        '--dls-dropdown-max-display-items': 4
      }"
      expanded
    />
  </section>
  <section>
    <h2>strict: </h2>
    <veui-autocomplete
      v-model="treeValue"
      :datasource="treeSuggestions"
      placeholder=""
      suggest-trigger="focus"
      strict
    />
  </section>
  <section>
    <h2></h2>
    <veui-autocomplete
      :datasource="suggestions"
      placeholder=""
      suggest-trigger="focus"
      strict
    />
  </section>
  <veui-button @click="value = 'male'">list->male</veui-button>
  <veui-button @click="treeValue = 'male'">tree->male</veui-button>
  <veui-button @click="switchDatasource"></veui-button>
</article>
</template>

<script>
import { Autocomplete, Button } from 'veui'

export default {
  name: 'autocomplete-demo',
  components: {
    'veui-autocomplete': Autocomplete,
    'veui-button': Button
  },
  data () {
    return {
      value: null,
      treeValue: '',
      inputValue: 'male',
      suggestions: [
        {
          value: 'male'
        },
        {
          value: 'female'
        }
      ],
      treeSuggestions: [
        {
          label: '1',
          options: ['1', '11']
        },
        {
          label: '2',
          options: [
            {
              label: '',
              options: [
                {
                  label: 'male',
                  value: 'male'
                }
              ]
            },
            {
              label: 'female',
              value: 'female'
            }
          ]
        }
      ]
    }
  },
  methods: {
    switchDatasource () {
      let tmp = this.suggestions
      this.suggestions = this.treeSuggestions
      this.treeSuggestions = tmp
    }
  }
}
</script>
