<template>
<article>
  <h1><code>&lt;veui-textarea&gt;</code></h1>
  <section>
    <h3> +  3  +  resize</h3>
    <veui-textarea
      v-model="value"
      line-number
      autofocus
      ui="s"
      rows="3"
      resizable
    />
  </section>
  <section>
    <h3></h3>
    <veui-textarea
      :value="fixed"
      composition
      line-number
      autofocus
      ui="s"
      rows="3"
      resizable
    />
    <h3></h3>
    <veui-textarea
      :value="fixed"
      line-number
      autofocus
      ui="s"
      rows="3"
      resizable
    />
    <h3>,  v-model value: {{ controlled1 }}</h3>
    <veui-textarea
      v-model="controlled1"
      composition
      line-number
      autofocus
      ui="s"
      rows="3"
      resizable
    />
    <h3>,  v-model value: {{ controlled2 }}</h3>
    <veui-textarea
      v-model="controlled2"
      line-number
      autofocus
      ui="s"
      rows="3"
      resizable
    />
    <h3>localValue{{ uncontrolled1 }}</h3>
    <veui-textarea
      ref="text2"
      composition
      line-number
      autofocus
      ui="s"
      rows="3"
      resizable
      @input="uncontrolled1 = $event"
    />
    <h3>localValue{{ uncontrolled2 }}</h3>
    <veui-textarea
      ref="text2"
      line-number
      autofocus
      ui="s"
      rows="3"
      resizable
      @input="uncontrolled2 = $event"
    />
  </section>
  <section>
    <h3> +  + </h3>
    <veui-textarea
      v-model="value"
      line-number
      select-on-focus
      autoresize
      placeholder=""
    />
  </section>
  <section>
    <h3></h3>
    <veui-textarea
      v-model="value"
      autoresize
    />
  </section>
  <section>
    <h3></h3>
    <veui-textarea
      v-model="value"
      readonly
    />
  </section>
  <section>
    <h3></h3>
    <veui-textarea
      v-model="value"
      disabled
    />
  </section>
  <section>
    <h3></h3>
    <veui-textarea
      v-model="value"
      invalid
    />
  </section>

  <section>
    <h3></h3>
    <section>
      <veui-textarea
        placeholder=""
        maxlength="5"
        strict
      />
    </section>
    <h3> 2</h3>
    <section>
      <veui-textarea
        maxlength="5"
        :get-length="getLength"
      />
    </section>
    <section>
      <veui-textarea
        placeholder=""
        maxlength="5"
        rows="2"
        line-number
        value="Lorem ipsum dolor sit amet consectetur adipisicing elit. Corporis inventore non cumque vero eligendi? Iure ex sint aut. Facilis doloribus facere ducimus consequatur ipsa reiciendis voluptates minima molestiae deserunt nemo."
      />
    </section>
  </section>
</article>
</template>

<script>
import bus from '../bus'
import { Textarea } from 'veui'

export default {
  name: 'button-demo',
  components: {
    'veui-textarea': Textarea
  },
  data () {
    return {
      value: '',
      fixed: '',
      controlled1: '',
      controlled2: '',
      uncontrolled1: '',
      uncontrolled2: ''
    }
  },
  mounted () {
    this.$children.forEach(child => {
      child.$on('click', () => {
        bus.$emit('log', child.$el.getAttribute('ui'))
      })
    })
  },
  methods: {
    getLength (val) {
      // eslint-disable-next-line no-control-regex
      return val.replace(/[^\x00-\xff]/g, 'aa').length
    }
  }
}
</script>

