<template>
<article>
  <h1><code>&lt;veui-tag&gt;</code></h1>
  <section>
    <veui-checkbox v-model="bordered"></veui-checkbox>
  </section>
  <section>
    <h2></h2>
    <div>
      <veui-tag
        v-for="size in sizes"
        :key="size"
        :ui="size + (bordered ? ' bordered' : '')"
      >
        {{ size }}
      </veui-tag>
    </div>
  </section>

  <section>
    <h2></h2>
    <div>
      <veui-tag
        v-for="type in types"
        :key="type"
        :type="type"
        :ui="bordered ? 'bordered' : ''"
      >
        {{ type }}
      </veui-tag>
    </div>
  </section>

  <section>
    <h2></h2>
    <div>
      <veui-tag
        v-for="(team, index) in teams2"
        :key="team"
        :type="types[index]"
        :ui="bordered ? 'bordered' : ''"
        removable
        @remove="handleRemove(team)"
      >
        {{ team }}
      </veui-tag>
    </div>
  </section>

  <section>
    <h2></h2>
    <div>
      <veui-tag
        v-for="(team, index) in teams"
        :key="team"
        :type="types[index]"
        removable
        :ui="'s' + (bordered ? ' bordered' : '')"
        @remove="handleRemove(team)"
      >
        {{ team }}
      </veui-tag>
    </div>
  </section>

  <section>
    <h2></h2>
    <div>
      <veui-tag
        :ui="bordered ? 'bordered' : ''"
        removable
        :removed="controlledRemoved"
        @remove="handleControlledRemove"
      >
        
      </veui-tag>
    </div>
  </section>

  <section>
    <h2></h2>
    <div>
      <veui-tag
        v-for="type in types"
        :key="type"
        :type="type"
        :selected.sync="selected"
        :ui="bordered ? 'bordered' : ''"
        selectable
      >
        {{ type }}
      </veui-tag>
    </div>
  </section>

  <section>
    <h2></h2>
    <div>
      <veui-tag
        v-for="type in types"
        :key="type"
        :type="type"
        :selected.sync="selected"
        :ui="bordered ? 'bordered' : ''"
        disabled
        selectable
      >
        {{ type }}
      </veui-tag>
    </div>
    <div style="margin-top: 20px">
      <veui-tag
        v-for="type in types"
        :key="type"
        :type="type"
        :ui="bordered ? 'bordered' : ''"
        disabled
        removable
      >
        {{ type }}
      </veui-tag>
    </div>
  </section>
</article>
</template>

<script>
import Vue from 'vue'
import { Tag, Checkbox } from 'veui'
import toast from 'veui/plugins/toast'
import confirm from 'veui/plugins/confirm'

Vue.use(toast)
Vue.use(confirm)

export default {
  name: 'tag-demo',
  components: {
    'veui-tag': Tag,
    'veui-checkbox': Checkbox
  },
  data () {
    return {
      teams: ['', '', '', '', ''],
      teams2: ['', '', '', '', ''],
      types: ['default', 'info', 'success', 'warning', 'error'],
      sizes: ['s', 'm', 'default'],
      selected: false,
      bordered: false,
      controlledRemoved: false
    }
  },
  methods: {
    handleRemove (name) {
      this.$toast.success(name + '')
    },
    handleControlledRemove () {
      this.$confirm('').then(confirmed => {
        if (confirmed) {
          this.controlledRemoved = true
        }
      })
    }
  }
}
</script>

<style scoped>
.veui-tag {
  margin-right: 15px;
}
</style>
